import React, { useState } from 'react';
import { Helmet } from 'react-helmet';
import { NavLink } from "react-router-dom";
import Image1 from '../assets/risk-analysis/1.png'
import Image2 from '../assets/risk-analysis/2.png'
import Image3 from '../assets/risk-analysis/3.png'
import Image4 from '../assets/risk-analysis/4.png'
import Image5 from '../assets/risk-analysis/5.png'
import ManualPDF from '../assets/Manual.pdf'




const ProjectedRisk = () => {
  const [slideIndex, setSlideIndex] = useState(1);
  const images = [{ id: 1, src: Image1, alt: 'Projected change in risk as per CMIP6 SSP585 scenerio. (2071 - 2100)' }, { id: 2, src: Image2, alt: 'Projected change in risk as per CMIP6 SSP585 scenerio. (2071 - 2100)' }, { id: 3, src: Image3, alt: 'Projected change in risk as per CMIP6 SSP585 scenerio. (2071 - 2100)' }, { id: 4, src: Image4, alt: 'Projected change in risk as per CMIP6 SSP585 scenerio. (2071 - 2100)' }, { id: 5, src: Image5, alt: 'Projected change in risk as per CMIP6 SSP585 scenerio. (2071 - 2100)' },];

  const handlePrevClick = () => {
    if (slideIndex === 1) {
      setSlideIndex(images.length);
    } else {
      setSlideIndex(slideIndex - 1);
    }
  };

  const handleNextClick = () => {
    if (slideIndex === images.length) {
      setSlideIndex(1);
    } else {
      setSlideIndex(slideIndex + 1);
    }
  };

  const handleImageClick = (id) => {
    setSlideIndex(id);
  };


  return (
    <div>
      <Helmet>
        <title>Projected Risk | GeoID</title>
      </Helmet>
      <div className="main-map">
        <div className="risk-analysis-list">
          <div className="heading">
            <h2>Indian Dams Risk Analysis</h2>
            <button className="BackButton"><NavLink to="/">&laquo; Back</NavLink></button>
          </div>
          <button className="classifyButton"><NavLink to="/projected-risk/dam-risk-analysis"><span>Individual Dam Risk Analysis </span></NavLink></button>
          <button className="user-guide"><a href={ManualPDF} target="_blank" rel="noreferrer"><span>User Guide</span></a></button>
        </div>
        <div className="right-side">
          <div className="risk-analysis">
            <div className="container">
              <div className="slideshow-main-img">
                <img src={images[slideIndex - 1].src} alt={images[slideIndex - 1].alt} />
                <div className="numbertext">{slideIndex} / {images.length}</div>
                <a className="prev" onClick={handlePrevClick}>❮</a>
                <a className="next" onClick={handleNextClick}>❯</a>

              </div>
              <div className="caption-container">
                <p> Projected change in risk as per CMIP6 SSP585 scenerio. (2071 - 2100)</p>
              </div>

              <div className="slide-row">
                {images.map((image) => (
                  <div className="risk-column" key={image.id}>
                    <img className="img-demo" src={image.src} style={{ width: '100%' }} onClick={() => handleImageClick(image.id)} alt={image.alt} />


                  </div>
                ))}
              </div>
            </div>
          </div>

        </div>

      </div>


    </div>
  )
}

export default ProjectedRisk
